.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.Comment {
	margin-top: 40px;
	width: 764px;
	height: 405px;
	overflow-y: auto;
	line-height: 1.5;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0px;
	padding-right: 0px;
	padding-top: 3.5px;
	margin: 0;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}